<template>
  <div>
    <fTitle>
      <template #title>Cascader 级联选择器</template>
      <template #introduce>当一个数据集合有清晰的层级结构时，可通过级联选择器逐级查看并选择。</template>
    </fTitle>
    <sTitle>
      <template #title>各种基础用法</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <h4>默认click触发子菜单</h4> <el-cascader
    v-model="value"
    :options="options"
    ></el-cascader>
          </div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h4>hover触发子菜单</h4> <el-cascader
    v-model="value"
    :options="options"
    :props="{ expandTrigger: 'hover' }"
    ></el-cascader>
          </div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <h4>clearable设置输入框可清空</h4><el-cascader :options="options" clearable></el-cascader></div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h4>仅在输入框中显示选项最后一级</h4><el-cascader :options="options" :show-all-levels="false"></el-cascader></div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <h4>可多选默认显示所有Tag</h4><el-cascader
    :options="options"
    :props="{ multiple: true }"
    clearable></el-cascader></div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h4>可多选默认折叠展示Tag</h4> <el-cascader
    :options="options"
    :props="{ multiple: true }"
    collapse-tags
    clearable></el-cascader></div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <h4>单选选择任意一级选项</h4> <el-cascader
    :options="options"
    :props="{ checkStrictly: true }"
    clearable></el-cascader></div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h4>多选选择任意一级选项</h4> <el-cascader
    :options="options"
    :props="{ multiple: true, checkStrictly: true }"
    clearable></el-cascader></div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <h4>单选可搜索filterable</h4> <el-cascader
    placeholder="试试搜索：指南"
    :options="options"
    filterable></el-cascader></div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h4>多选可搜索filterable</h4> <el-cascader
    placeholder="试试搜索：指南"
    :options="options"
    :props="{ multiple: true }"
    filterable></el-cascader></div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <h4>可以自定义备选项的节点内容</h4> <el-cascader :options="options">
  <template slot-scope="{ node, data }">
    <span>{{ data.label }}</span>
    <span v-if="!node.isLeaf"> ({{ data.children.length }}) </span>
    <!-- node.isLeaf是叶节点的意思 -->
  </template>
</el-cascader></div></el-col>
        </el-row>
      </div></div>
      <sTitle>
      <template #title>级联面板</template>
      <template #introduce>级联面板是级联选择器的核心组件，与级联选择器一样，有单选、多选、动态加载等多种功能。</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-cascader-panel :options="options"></el-cascader-panel>
      </div></div>
  </div>

</template>

